import React from "react";
import './Endpoint.css'
import {ChoiceGroup, Label, PrimaryButton} from "@fluentui/react";
import {Checkbox} from '@fluentui/react';
import {Toggle} from '@fluentui/react/lib/Toggle';
import {TextField} from '@fluentui/react/lib/TextField';
import ResourcePanel from "./ResourcesPanel";


class Endpoint extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            function_list: [],
            data: {},
            show_list: [],
        }
    }

    getListArray(function_list) {
        let ret = []
        for (let i in function_list) {
            let methods = function_list[i].methods
            let resource_id = function_list[i].resource_id
            ret.push({name: i, methods: methods, resource_id: resource_id})
        }
        return ret
    }

    getPreview(props) {
        return <div>{props.name}</div>
    }

    getCreate() {
        return <EndpointEdit create={true}/>
    }

    getEdit(props) {
        return <EndpointEdit create={false} path={props.name}/>
    }

    componentDidMount() {
        fetch(this.backend_path + '/endpoint/get_all')
            .then(res => res.json())
            .then(json => {
                let temp = []
                for (let i in json) {
                    temp.push(i)
                }
                let show_list = this.getListArray(json)
                this.setState({function_list: temp, data: json, show_list: show_list})
            })
    }

    render() {
        return (<div>
            <ResourcePanel
                list={this.state.show_list}
                getCreate={this.getCreate}
                getPreview={this.getPreview}
                getEdit={this.getEdit}/>
        </div>)
    }
}


class EndpointEdit extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            path: "",
            methods: "GET",
            rs_type: "code",
            data: "",
            auth: "none",
            cache: false,
            all_methods_status: {"GET": true, "POST": false, "PATCH": false, "DELETE": false}
        };
        this.all_methods = []
        for (let i in this.state.all_methods_status) {
            this.all_methods.push(i)
        }
        this.file = null
    }

    updateMethods() {
        let new_method = ""
        for (let i in this.state.all_methods_status) {
            if (true === this.state.all_methods_status[i]) {
                new_method += i + ","
            }
        }
        if (new_method.length > 0) {
            new_method = new_method.substring(0, new_method.length - 1)
        }

        this.setState({methods: new_method})
    }

    onSave() {
        let url = this.backend_path + "/endpoint/create"
        let data = {
            path: this.props.create ? this.state.path : this.props.path,
            methods: this.state.methods,
            rs_type: this.state.rs_type,
            data: this.state.data,
            auth: this.state.auth,
            cache: this.state.cache,
        }
        this.postData(url, data).then(r => {
            console.log(r)
        })
    }

    selectMethod(method, checked) {
        for (let i in this.state.all_methods_status) {
            if (method === i) {
                let all_methods_status = this.state.all_methods_status
                all_methods_status[i] = checked
                break
            }
        }
        this.updateMethods()
    }

    render() {
        return (
            <div>

                <div id="ep_form"
                     className={"ep_form"}
                >
                    {
                        <div className={"endpoint"}>


                            <TextField label="自定义路径"
                                       required
                                       description="不要以/结尾"
                                       prefix="/"
                                       disabled={!this.props.create}
                                       defaultValue={this.props.path === undefined ? "" : this.props.path.substring(1)}
                                       onChange={(i, j) => {
                                           this.setState({
                                               path: "/" + j
                                           })
                                       }}
                            />

                        </div>
                    }

                    <Label required> 请求方法 :</Label>
                    {
                        this.all_methods.map(method => {
                            return <Checkbox label={method}
                                             key={method}
                                             defaultChecked={this.state.all_methods_status[method]}
                                             onChange={(i, j) => {
                                                 this.selectMethod(method, j)
                                             }}
                            />
                        })
                    }

                    <ChoiceGroup defaultSelectedKey={this.state.rs_type}
                                 options={[
                                     {key: 'code', text: 'code'},
                                     {key: 'img', text: 'img'},
                                     {key: 'application/json', text: 'json'},
                                     {key: 'text/plain', text: 'text'},
                                     {key: 'other', text: 'other'}
                                 ]}
                                 label="资源类型:"
                                 required={true}
                                 onChange={(i, j) => this.setState({rs_type: j.key})}
                    />


                    <div>
                        {
                            this.state.rs_type === "code" &&
                            <div>
                                function name
                                <TextField name="function_name" type="text"
                                           onChange={(i, j) => {
                                               this.setState({
                                                   data: j
                                               })
                                           }}
                                />
                            </div>
                        }
                        {
                            this.state.rs_type === "img" &&
                            <div>
                                <img alt={""}/>
                                select file<input name={"file"} type="file"/>
                            </div>
                        }
                        {
                            this.state.rs_type === "application/json" &&
                            <div><textarea name="data">json</textarea></div>
                        }
                        {
                            this.state.rs_type === "text/plain" &&
                            <div>
                                <textarea name="data">text</textarea>
                            </div>
                        }
                        {
                            this.state.rs_type === "other" &&
                            <div>
                                mime<input name={"rs_type"} type="text"/>
                                <input name="file" type="file"/>
                            </div>
                        }
                    </div>

                    <ChoiceGroup defaultSelectedKey={this.state.auth}
                                 options={[
                                     {key: 'none', text: 'none'},
                                     {key: 'cookie', text: 'cookie'},
                                     {key: 'header', text: 'header'}
                                 ]}
                                 label="鉴权:"
                                 required={true}
                                 onChange={(i, j) => {
                                     this.setState({
                                         auth: j.key
                                     })
                                 }}
                    />

                    <Toggle label="缓存" onText="开启" offText="关闭"
                            defaultChecked={this.state.cache}
                            onChange={(i, j) => {
                                this.setState({
                                    cache: j
                                })
                            }}
                    />


                    <div>
                        {
                            this.props.create && <PrimaryButton type="button" onClick={() => this.onSave()} text="创建"/>
                        }
                        {
                            !this.props.create && <PrimaryButton type="button" onClick={() => this.onSave()} text="保存"/>
                        }

                    </div>
                </div>
            </div>
        )
    }
}

export default Endpoint