import {CommandBar} from "@fluentui/react/lib/CommandBar";
import React from "react";
import {DetailsList, DetailsRow} from "@fluentui/react/lib/DetailsList";
import {Nav} from "@fluentui/react/lib/Nav";
import {DefaultButton, Panel, PrimaryButton} from "@fluentui/react";
import './ResourcePanel.css'
import {Label} from "@fluentui/react/lib/Label";

class ResourceList extends React.Component {

    onSelect(no) {
        if (this.props.onSelect !== undefined) {
            this.props.onSelect(no)
        }
    }

    getContent() {

        if (this.props.list === undefined || this.props.list.length === 0) {
            return <div><Label>no resource here</Label></div>
        }

        let items = []
        for (let i in this.props.list) {
            items[i] = {}
            for (let j in this.props.list[i]) {
                items[i][j] = this.props.list[i][j]
            }
            items[i].name = <div onClick={() => this.onSelect(i)}>{items[i].name} </div>
        }

        return <DetailsList items={items}
                            setKey="set"
                            onRenderRow={(props) => {
                                return <DetailsRow {...props} onClick={() => {
                                }}/>
                            }}
                            checkboxVisibility={1}
                            checkButtonAriaLabel="select row"
                            onItemInvoked={(item, no) => {
                                this.onSelect(no)
                            }}
        />
    }

    render() {
        return (
            <div>
                {
                    this.getContent()
                }
            </div>

        )
    }
}

class ResourceFrame extends React.Component {
    render() {
        return (
            <div className={"resource_panel_main_frame"}>
                {/**************************** nav ****************************/}
                <div>
                    <Nav
                        onRenderGroupHeader={(group) => {
                            return <h3>{group.name}</h3>;
                        }}
                        ariaLabel="Nav example with custom group headers"
                        groups={[
                            {
                                name: '资源列表',
                                links:
                                    this.getNavList().map((i) => {
                                            return {name: i.nav, key: i.nav, target: '_blank'}
                                        }
                                    )
                            }
                        ]}
                        onLinkClick={(i, j) => {
                            this.onSelect(j.key)
                        }}
                    />
                </div>

                <div style={{padding: '29px'}}>

                    {
                        this.state.modeCreate && this.getCreate()
                    }
                    {
                        this.state.modeCreate && this.getEdit(this.state.selectObject, () => {
                        })
                    }
                </div>
            </div>
        )
    }
}

class ResourcePanel extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            modeList: false,
            modePreview: false,
            modeEdit: false,
            modeCreate: false,
            selectObjectNo: 0,
        };
    }

    modeReset() {
        this.setState({
            modeList: false,
            modePreview: false,
            modeEdit: false,
            modeCreate: false,
        })
    }

    modePreview() {
        this.modeReset()
        this.setState({modePreview: true})
    }

    modeCreate() {
        this.modeReset()
        this.setState({modeCreate: true})
    }

    modeEdit() {
        this.modeReset()
        this.setState({modeEdit: true})
    }

    onSelectItem(no) {
        this.setState({selectObjectNo: no})
        this.modePreview()
    }

    getCreate() {
        if (this.props.getCreate === undefined) {
            return <div/>
        } else {
            return this.props.getCreate()
        }
    }

    getEdit() {
        if (this.props.getEdit === undefined || this.props.list.length === 0) {
            return <div/>
        } else {
            return this.props.getEdit(this.props.list[this.state.selectObjectNo])
        }
    }


    getPreview() {
        if (this.props.getPreview === undefined || this.props.list.length === 0) {
            return <div/>
        } else {
            return this.props.getPreview(this.props.list[this.state.selectObjectNo])
        }
    }

    render() {
        return (
            <div>

                <div>
                    <CommandBar
                        items={[
                            {
                                key: 'newItem',
                                text: 'New',
                                cacheKey: 'myCacheKey', // changing this key will invalidate this item's cache
                                iconProps: {iconName: 'Add'},
                                onClick: () => {
                                    this.modeCreate()
                                },
                            },
                            {
                                key: 'upload',
                                text: 'Upload',
                                iconProps: {iconName: 'Upload'},
                                //href: 'https://developer.microsoft.com/en-us/fluentui',
                            },
                            {
                                key: 'share',
                                text: 'Share',
                                iconProps: {iconName: 'Share'},
                                //onClick: () => console.log('Share'),
                            },
                            {
                                key: 'download',
                                text: 'Download',
                                iconProps: {iconName: 'Download'},
                                //onClick: () => console.log('Download'),
                            },
                        ]}
                        //overflowItems={this._overflowItems}
                        //overflowButtonProps={this.overflowProps}
                        farItems={[
                            {
                                key: 'info',
                                text: 'Info',
                                // This needs an ariaLabel since it's icon-only
                                ariaLabel: 'Info',
                                iconOnly: true,
                                iconProps: {iconName: 'Info'},
                                //onClick: () => console.log('Info'),
                            },
                        ]}
                        ariaLabel="Inbox actions"
                        primaryGroupAriaLabel="Email actions"
                        farItemsGroupAriaLabel="More actions"
                    />
                </div>

                {
                    <ResourceList list={this.props.list} onSelect={(item) => {
                        this.onSelectItem(item)
                    }}/>
                }


                {/********     preview       *******/}
                <Panel
                    headerText="预览"
                    isOpen={this.state.modePreview}
                    onDismiss={() => {
                        this.modeReset()
                    }}
                    // You MUST provide this prop! Otherwise screen readers will just say "button" with no label.
                    //closeButtonAriaLabel="Close"
                    onRenderFooterContent={() => {
                        return <div>
                            <PrimaryButton onClick={this.modeEdit.bind(this)} text={"编辑"}
                                           styles={{root: {marginRight: 8}}}/>
                            <DefaultButton onClick={this.modeReset.bind(this)} text={"取消"}/>
                        </div>
                    }}
                    isLightDismiss={true}
                    //isFooterAtBottom={true}
                >
                    {this.getPreview()}
                </Panel>

                {/*********     create       *********/}
                <Panel
                    headerText="创建"
                    isOpen={this.state.modeCreate}
                    onDismiss={() => {
                        this.modeReset()
                    }}
                    // You MUST provide this prop! Otherwise screen readers will just say "button" with no label.
                    //closeButtonAriaLabel="Close"
                    onRenderFooterContent={() => {
                        return <div>
                            <DefaultButton onClick={this.modeReset.bind(this)} text={"取消"}/>
                        </div>
                    }}
                    isLightDismiss={true}
                    //isFooterAtBottom={true}
                >
                    {this.getCreate()}
                </Panel>

                {/*********     edit       *********/}
                <Panel
                    headerText="编辑"
                    isOpen={this.state.modeEdit}
                    onDismiss={() => {
                        this.modeReset()
                    }}
                    // You MUST provide this prop! Otherwise screen readers will just say "button" with no label.
                    //closeButtonAriaLabel="Close"
                    onRenderFooterContent={() => {
                        return <div>
                            <DefaultButton onClick={this.modeReset.bind(this)} text={"取消"}/>
                        </div>
                    }}
                    isLightDismiss={true}
                    //isFooterAtBottom={true}
                >
                    {this.getEdit()}
                </Panel>


                {this.state.modeList && <ResourceFrame/>}
            </div>
        )
    }
}


export default ResourcePanel

